
import { defineComponent } from 'vue'
import anime from 'animejs'
import 'animate.css'
import AppBtn from '@/components/AppBtn.vue'
import AppWriter from '@/components/AppWriter.vue'
import AppAvatarScreen from '@/components/AppAvatarScreen.vue'

export default defineComponent({
  name: 'HomeView',
  data (): {
    [key: string]: any
    } {
    return {
      step: 0,
      text: '–ü—Ä–∏–≤–µ—Ç',
      interval: null
    }
  },
  components: {
    AppBtn,
    AppWriter,
    AppAvatarScreen
  },
  methods: {
    onWriterNext () {
      this.step++

      setTimeout(() => {
        (this.$refs.music as HTMLAudioElement).play()
      }, 2000)
    }
  },
  watch: {
    step (step) {
      if (step === 7) {
        clearInterval(this.interval)

        document.body.style.background = 'red'

        setTimeout(() => {
          document.querySelector('.error')!.className += ' animate__animated animate__hinge'

          setTimeout(() => {
            this.step++

            document.body.style.background = 'initial'

            setTimeout(() => this.step++, 3000)
          }, 2000)
        }, 3000)
      }
    }
  },
  mounted () {
    document.addEventListener('keydown', (e) => {
      this.step = parseInt(e.key)
      clearInterval(this.interval)
    })

    // Create a timeline with default parameters
    // const tl = anime.timeline({
    //   autoplay: true,
    //   easing: 'easeOutExpo',
    //   duration: 5750
    // })

    this.interval = setInterval(() => this.step++, 3000)

    // tl
    //   .add({
    //     targets: '.square',
    //     opacity: 1,
    //     duration: 2000,
    //     complete: () => {
    //       this.step = 1
    //     }
    //   })
    //   .add({
    //     targets: '.circle',
    //     translateX: 250
    //   })
    //   .add({
    //     targets: '.triangle',
    //     translateX: 250
    //   })
  },
  unmounted () {
    clearInterval(this.interval)
  }
})
